// import 'babel-polyfill';
import projects from './projects';
require('./css/styles.scss');
require('./img/');

console.log(projects);

for (const i in projects) {
  let ribbons = document.getElementById('project-ribbons');
  let cssClass = (i % 2 === 0 ? 'ribbon-dark' : 'ribbon-light');
  let newDiv = document.createElement('DIV');
  let newDivHtml = `
    <div class="ribbon ${cssClass}">
      <div class="project-info">
        <h3>${projects[i].title}</h3>
        <p>${projects[i].description}</p>
        <ul class="project-body-list">
    `;

    for (const j in projects[i].body) {
      newDivHtml += '<li>' + projects[i].body[j] + '</li>';
    };

    newDivHtml += '</ul><p>';

    for (const j in projects[i].links) {
      newDivHtml += `
        <span class="project-link">
          <a href="${projects[i].links[j].href}" target="_blank">
            ${projects[i].links[j].text}
          </a>
        </span>
      `
    };

    newDivHtml += `
      </p>
        </div>
        <div class="img">
          <a href="${projects[i].links[0].href}" target="_blank">
            <img src="${projects[i].img}" />
          </a>
        </div>
      </div>
    `;

  newDiv.innerHTML = newDivHtml;
  console.log(newDiv);
  ribbons.appendChild(newDiv);
}



